.soft-model{
    width: 20%;
    height: 160px;
    background-color: #fff;
    margin-bottom: 20px;
    margin-right: 20px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid #ccc;
 }
 
 .soft-model-top{
    width: 100%;
    height: 100px;
    border-radius: 5px;
    background-color: #ffffff;
 }
 
 .soft-model-title{
    font: 40;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-top: 10px;
    align-self: flex-end;
    justify-self: flex-end;
    text-align: center;
 }

 .soft-img{
    border-radius: 5px;
 }