/* 这个是title 和 tags的blocks 区块 */
.block-tag{
    display: flex;
    width: 40%;
    height: 50px;
    background-color: #fff;
    border-radius: 5px;
    border: 1px dotted rgba(98, 156, 225, 0.7);
    cursor: pointer;
    align-content: center;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.block-card{
    width: 40%;
    height: 210px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    align-content: space-between;
    cursor: pointer;
    margin-bottom: 30px;
  
}

.card-model{
    display: flex;
    flex: 1;
    height: 100%;
    align-content: center;
    align-items: center;
    justify-content: space-between;
}
.card-title{
    align-self: center;
    justify-self: center;
    display: flex;
    flex: 1;
    align-self: center;
    margin-left: 20px;
    overflow-x: auto;
    margin-right: 10px;
}
.card-footer{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    align-content: center;
    justify-content: space-between;
}

.card-keyword{
    display: flex;
    justify-content: space-around;
    flex: 1;
    align-content: center;
    align-items: flex-end;
}
.card-context{
    width: 100%;
    flex: 1;
    display: flex;
    max-height: 80px;
    min-height: 80px;
    justify-content: start;
    overflow: auto;
}