.hoc-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
}

.hoc-model-container{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    margin-top: 20px;
    flex-wrap:wrap;
    justify-content: space-around;
    max-height: 80vh;
    min-height: 70vh;
    overflow: scroll; 
}

.hoc-title{
  font: bold;
  font-size: 20px;
  height: 30px;
  width: 100%;
  align-self: start;
  display: flex;
  justify-self: flex-start;
  border-bottom: 1px solid #ff99ff;
}
.hoc-title::before{
    width: 3px;
    content: '';
    background-color: #ff0000;
    display: inline-block;
    margin-right: 10px;
    height: 15px;
    margin-top: 9px;
}
